.note-list {
    padding: 20px;
    color: white;
    border-right: 1px solid white;
    display: flex;
    width: 30%;
    flex-direction: column;
    /* overflow-y: scroll;  */
}


.note-list__note {
    padding: 5px;
    font-size: 20px;
    display: flex;
    justify-content: space-between;
}

.note-list__note__delete {
    align-self: center;
    cursor: pointer;
    width: 30px;
    height: 30px;
    min-height: 30px;
    min-width: 30px;
    border-radius: 50%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.note-list__note__delete img {
    width: 16px;
}

.note-list__add-note {
    align-self: center;
    cursor: pointer;
    width: 40px;
    height: 40px;
    min-height: 40px;
    min-width: 40px;
    border-radius: 50%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
}

.note-list__add-note img {
    width: 20px;
}

.note-list__header {
    margin-top: 0;
}

.note-list__note__name {
    cursor: pointer;
}

.note-list__note__name--selected {
    color: red;
}

@media only screen and (min-width: 1250px){
    .note-list {
        width: 20%;
    }
}
@media only screen and (min-width: 1800px) {
    .note-list {
        width: 15%;
    }
}
@media only screen and (max-width: 870px) {
    .note-list {
        width: 40%;
    }
}
@media only screen and (max-width: 780px) {
    .note-list__note {
        font-size: 18px;
    }
    .note-list__header {
        font-size: 22px;
    }
    .note-list__note__delete {
        width: 24px;
        height: 24px;
        min-height: 24px;
        min-width: 24px;
    }

    .note-list__note__delete img {
        width: 12px;
    }

    .note-list__add-note {
        width: 30px;
        height: 30px;
        min-height: 30px;
        min-width: 30px;
        margin: 20px 0;
    }

    .note-list__add-note img {
        width: 20px;
    }
}