.note {
    padding: 30px;
    display: flex;
    flex-direction: column;
    width: 70%;
}

.note__name {
    font-size: 20px;
    margin-bottom: 30px;
    padding: 10px;
    background-color: #020202;
    border: none;
    border-bottom: 1px solid white;
    color: white;
}

.note__body {
    font-size: 16px;
    padding: 10px;
    background-color: #020202;
    border: none;
    border-bottom: 1px solid white;
    color: white;
}

@media only screen and (min-width: 1250px) {
    .note {
        width: 80%;
    }
}
@media only screen and (min-width: 1800px) {
    .note {
        width: 85%;
    }
}
@media only screen and (max-width: 870px) {
    .note {
        width: 60%;
    }
}
@media only screen and (max-width: 780px) {
    .note__name {
        font-size: 18px;
        margin-bottom: 20px;
        padding: 8px;
    }

    .note__body {
        font-size: 14px;
        padding: 8px;
    }
}